export function capitalizeFirstLetter(str) {
    if (typeof str !== 'string' || str.length === 0) {
      return '';
    }
    
    //       
    const firstLetter = str.charAt(0).toUpperCase();
    
    //   ,    
    const restOfString = str.slice(1);
    
    //  
    return firstLetter + restOfString;
}